<template>
  <div class="container">
      <div class="google-map" id="map"></div>
  </div>
</template>

<script>
export default {
  name: 'Set',
  data() {
    return {
      map: null
    }
  },
  mounted() {
    this.initMap()
    this.setMarker()
  },
  methods: {
    // 
    initMap() {
      console.log(this.$store.state.lat)
      console.log(this.$store.state.lng)
      //  Map  map  id  map 
      this.map = new google.maps.Map(document.getElementById("map"), {
        // 
        center: { lat: Number(this.$store.state.lat), lng: Number(this.$store.state.lng) },
        //  0-20
        zoom: 10,
        // 
        maxZoom: 20,
        // 
        minZoom: 3,
        // 
        streetViewControl: false,
        // 
        mapTypeControl: true
      })
    },
    // 
    setMarker() {
      // 
      const marker = new google.maps.Marker({
        // 
        position: { lat: Number(this.$store.state.lat), lng: Number(this.$store.state.lng) },
        // 
        map: this.map
      })
    }
  },
  activated () {
    this.initMap()
    this.setMarker()
  },
  deactivated () {
    this.initMap()
    this.setMarker()
  }
}
</script>

<style lang="stylus" scoped>
  .container                  //map
      position: absolute
      width: 100%
      height: 100%
    .google-map
        width: 100%
        height: 100%

</style>



